    //return res.index = -1return res.index = -2
    ReturnIndexOfAttack(initiator,ballistic){
        //initiator
        //ballistic:bool,//true
        let res = {
            index : -1,
            ballistic : false,
        }
        let index = -1;
        let index_max = initiator.current_attr.att - initiator.position - 1;
        //
        if(ballistic && !initiator.IsInsight()){
            let rand = Math.floor(Math.random()*2);
            if(rand == 0){
                //
                let arr_1 = [];
                //
                for(let i=0;i<3;i++){
                    if(this.vue.teams[initiator.belong][i].position != initiator.position && this.vue.teams[initiator.belong][i].exist){
                        arr_1.push(i);
                    }
                }
                //,
                if(arr_1.length == 0){ 
                    //attacker,Math.abs(attacker.position - 2)position
                    for(let i = (Math.abs(initiator.position - 2)) + 1;i<3;i++){
                        if(this.vue.teams[initiator.belong][i].exist == false){
                            //index
                            index_max ++;
                            if(index_max > 2){
                                index_max = 2;//
                            }
                        }
                    }
                    if(index_max < 0){
                        //
                        res.index = -1;
                        res.ballistic = false;//
                        return res;
                    }
                    let arr_new = [];
                    //
                    for(let i=0;i<3;i++){
                        if(this.vue.teams[(initiator.belong + 1)%2][i].exist){
                            arr_new.push(i);
                        }
                    }
                    if(arr_new.length == 0){
                        res.index = -2;
                        res.ballistic = false;//
                        return res;
                    }
                    let length = arr_new.length - 1;
                    for(let i=0;i<length-index_max;i++){
                        arr_new.shift();//arr---
                    }
                    index = arr_new[Math.floor(Math.random()*arr_new.length)];
                    res.index = index;
                    res.ballistic = false;//
                    //
                    return res;
                }
                let seq = initiator.current_attr.att - initiator.position;
                for(let i=0;i<(2 - Math.abs(seq));i++){
                    if(seq > 0){
                        arr_1.shift();
                    }else if(seq < 0){
                        arr_1.pop();
                    }
                }
                //
                if(arr_1.length <= 0){
                    res.index = -2;
                    res.ballistic = true;
                    return res;
                }

                index = arr_1[Math.floor(Math.random()*arr_1.length)];
                
                res.index = index;
                res.ballistic = true;
                return res;
            }
        }
        //initiator,Math.abs(initiator.position - 2)position
        for(let i = (Math.abs(initiator.position - 2)) + 1;i<3;i++){
            if(this.vue.teams[initiator.belong][i].exist == false){
                //index
                index_max ++;
                if(index_max > 2){
                    index_max = 2;//
                }
            }
        }
        if(index_max < 0){
            //
            res.index = -1;
            res.ballistic = false;//
            return res;
        }
        let arr = [];
        //
        for(let i=0;i<3;i++){
            if(this.vue.teams[(initiator.belong + 1)%2][i].exist){
                arr.push(i);
            }
        }
        if(arr.length == 0){
            res.index = -2;
            res.ballistic = false;//
            return res;
        }
        let length = arr.length -index_max - 1;
        for(let i=0;i<length;i++){
            arr.shift();//arr---
        }
        index = arr[Math.floor(Math.random()*arr.length)];
        //
        res.index = index;
        res.ballistic = false;//
        //
        for(let i=0;i<3;i++){
            if(this.vue.teams[(initiator.belong + 1)%2][i].exist && this.vue.teams[(initiator.belong + 1)%2][i].IsIntervene()){

                let report = new Report();
                report.InitVersaReport(this.vue.teams[(initiator.belong + 1)%2][res.index],1,``);
                this.vue.REPORT_HEAP.push(report);
                //
                res.index = i;
                break;

            }
        }
        //
        return res;
    }
