    ConsumeDodge(){
        for(let i=0;i<this.STATUS.length;i++){
            if(this.STATUS[i].status_type == '' && this.STATUS[i].once){
                this.STATUS.splice(i,1);
                return;
            }
        }
    }
    //,
    ConsumeOnceDamageVarBlock(initiator,damage_type){
        let addition = 0;
        let duplicate_arr = [];
        let temp = [];
        //this
        if(initiator){
            for(let i=0;i<this.DAMAGE_VAR_ONCE_HEAP.length;i++){
                if((this.DAMAGE_VAR_ONCE_HEAP[i].damage_type == damage_type || this.DAMAGE_VAR_ONCE_HEAP[i].damage_type == 'all') 
                && this.DAMAGE_VAR_ONCE_HEAP[i].attack && !this.DuplicateCheck(duplicate_arr,this.DAMAGE_VAR_ONCE_HEAP[i].tactics_type)){
                    if(this.DAMAGE_VAR_ONCE_HEAP[i].addition){
                        addition += this.DAMAGE_VAR_ONCE_HEAP[i].value;
                    }
                    else{
                        addition -= this.DAMAGE_VAR_ONCE_HEAP[i].value;
                    }
                    duplicate_arr.push(this.DAMAGE_VAR_ONCE_HEAP[i].tactics_type);
                }
                else{
                    temp.push(this.DAMAGE_VAR_ONCE_HEAP[i])
                }
            }
        }
        else{
            for(let i=0;i<this.DAMAGE_VAR_ONCE_HEAP.length;i++){
                if((this.DAMAGE_VAR_ONCE_HEAP[i].damage_type == damage_type || this.DAMAGE_VAR_ONCE_HEAP[i].damage_type == 'all') 
                && !this.DAMAGE_VAR_ONCE_HEAP[i].attack && !this.DuplicateCheck(duplicate_arr,this.DAMAGE_VAR_ONCE_HEAP[i].tactics_type)){
                    if(this.DAMAGE_VAR_ONCE_HEAP[i].addition){
                        addition += this.DAMAGE_VAR_ONCE_HEAP[i].value;
                    }
                    else{
                        addition -= this.DAMAGE_VAR_ONCE_HEAP[i].value;
                    }
                    duplicate_arr.push(this.DAMAGE_VAR_ONCE_HEAP[i].tactics_type);
                }
                else{
                    temp.push(this.DAMAGE_VAR_ONCE_HEAP[i])
                }
            }
        }
        this.DAMAGE_VAR_ONCE_HEAP = temp;

        return addition;
    }
    //
    ReturnCommonAttackCoefficient(){
        let coefficient = 0;
        let temp = [];
        let type = [];
        let lock = false;
        for(let i=0;i<this.DAMAGE_VAR_HEAP.length;i++){
            if(this.DAMAGE_VAR_HEAP[i].damage_type == 'common_attack'){
                
                for(let j=0;j<type.length;j++){
                    //
                    if(type[j] == this.DAMAGE_VAR_HEAP[i].damage_type && !this.DAMAGE_VAR_HEAP[i].once){
                        temp.push(this.DAMAGE_VAR_HEAP[i]);
                        lock = true;
                    }
                }
                if(!lock){
                    type.push(this.DAMAGE_VAR_HEAP[i].damage_type);
                    if(this.DAMAGE_VAR_HEAP[i].addition){
                        
                        coefficient += this.DAMAGE_VAR_HEAP[i].value;
                    }
                    else{
                        coefficient -= this.DAMAGE_VAR_HEAP[i].value;
                    }
                    if(!this.DAMAGE_VAR_HEAP[i].once){
                        temp.push(this.DAMAGE_VAR_HEAP[i])
                    }
                }else{
                    lock = false;
                }
            }
            else{
                temp.push(this.DAMAGE_VAR_HEAP[i])
            }
        }
        this.DAMAGE_VAR_HEAP = temp;
        if(coefficient < -90){
            coefficient = -90;
        }
        coefficient = coefficient/100;
        return coefficient;
    }
    //,,
    ReturnInitiatorCoefficient(initiator,damage_type){
        let coefficient = 0;
        //DAMAGE_VAR_HEAP
        let arr_length = initiator.DAMAGE_VAR_HEAP.length;
        let temp = [];
        for(let i=0;i<arr_length;i++){
            if((initiator.DAMAGE_VAR_HEAP[i].damage_type == damage_type || initiator.DAMAGE_VAR_HEAP[i].damage_type == 'all') && initiator.DAMAGE_VAR_HEAP[i].attack){
                //
                if(initiator.DAMAGE_VAR_HEAP[i].addition){
                    coefficient += initiator.DAMAGE_VAR_HEAP[i].value;
                }
                //
                else{
                    coefficient -= initiator.DAMAGE_VAR_HEAP[i].value;
                }
                //
                if(!initiator.DAMAGE_VAR_HEAP[i].once){
                    temp.push(initiator.DAMAGE_VAR_HEAP[i]);
                }
            }else{
                temp.push(initiator.DAMAGE_VAR_HEAP[i]);
            }
        }
        initiator.DAMAGE_VAR_HEAP = temp;
        let once_var = initiator.ConsumeOnceDamageVarBlock(true,damage_type);
        coefficient += once_var
        return coefficient;//
    }
    //,,
    ReturnReceiverCoefficient(receiver,damage_type){
        let coefficient = 0;
        let arr_length = receiver.DAMAGE_VAR_HEAP.length;
        let temp = [];
        for(let i=0;i<arr_length;i++){
            if((receiver.DAMAGE_VAR_HEAP[i].damage_type == damage_type || receiver.DAMAGE_VAR_HEAP[i].damage_type == 'all') && !receiver.DAMAGE_VAR_HEAP[i].attack){
                if(receiver.DAMAGE_VAR_HEAP[i].addition){
                    coefficient += receiver.DAMAGE_VAR_HEAP[i].value;
                }
                else{
                    coefficient -= receiver.DAMAGE_VAR_HEAP[i].value;
                }
                //
                if(!receiver.DAMAGE_VAR_HEAP[i].once){
                    temp.push(receiver.DAMAGE_VAR_HEAP[i]);
                }
            }else{
                temp.push(receiver.DAMAGE_VAR_HEAP[i]);
            }
        }
        receiver.DAMAGE_VAR_HEAP = temp;
        let once_var = receiver.ConsumeOnceDamageVarBlock(false,damage_type);
        coefficient += once_var
        return coefficient;//
    }
