    //
    ConsumeSTATUS_DAMAGE(){
        let temp = [];
        for(let i=0;i<this.STATUS_DAMAGE.length;i++){
            //
            let initiator = this.vue.teams[this.STATUS_DAMAGE[i].origin_team][this.STATUS_DAMAGE[i].origin_warrior]
            //
            if(!this.STATUS_DAMAGE[i].isCure){
                //
                if(this.STATUS_DAMAGE[i].damage_type == 'ad'){
                    let damage = this.CountAdDamage(initiator,this,this.STATUS_DAMAGE[i].value,false)
                    damage = Math.floor(damage * 0.8);
                    this.current_attr.hp -= damage;
                    //
                    let report = new Report();
                    report.InitVersaReport(this,2,`${this.STATUS_DAMAGE[i].status_type}${damage}(${this.current_attr.hp})`);
                    this.vue.REPORT_HEAP.push(report);

                    this.ExecuteAttackedBlock(initiator);
                    //
                    if(this.STATUS_DAMAGE[i]){
                        initiator.STATISTICS.tact += damage;
                        initiator.TACTICS_STACK[this.STATUS_DAMAGE[i].tactics_index].statistics.damage += damage;
                    }
                    
                }
                //
                else if(this.STATUS_DAMAGE[i].damage_type == 'tact'){
                    let damage = 0;
                    //
                    if(this.STATUS_DAMAGE[i].tactics_type != 'command'){
                        damage = this.CountTactDamage(initiator,this,this.STATUS_DAMAGE[i].value,false,false);
                        damage = Math.floor(damage * 0.7);
                    }
                    //
                    else{
                        damage = this.CountCommandTactDamage(initiator,this,this.STATUS_DAMAGE[i].value,false,false);
                        damage = Math.floor(damage * 0.2);
                    }
                    
                    this.current_attr.hp -= damage;
                    if(this.current_attr.hp < 0){
                        this.current_attr.hp =0;
                        this.exist = false;
                    }
                    //
                    let report = new Report();
                    report.InitVersaReport(this,2,`${this.STATUS_DAMAGE[i].status_type}${damage}(${this.current_attr.hp})`);
                    this.vue.REPORT_HEAP.push(report);

                    this.ExecuteAttackedBlock(initiator);

                    //
                    if(this.STATUS_DAMAGE[i]){
                        initiator.STATISTICS.tact += damage;
                        initiator.TACTICS_STACK[this.STATUS_DAMAGE[i].tactics_index].statistics.damage += damage;
                    }
                    
                }
            }
            //
            else{
                if(this.IsCannotCure()){
                    //
                    let report_2 = new Report();
                    report_2.InitVersaReport(this,2,``);
                    this.vue.REPORT_HEAP.push(report_2);
                    
                }
                else{
                    let cure = this.CountCure(initiator,this,"command",this.STATUS_DAMAGE[i].value,4);
                    this.current_attr.hp += cure;
                    //
                    let report = new Report();
                    report.InitVersaReport(this,2,`${this.STATUS_DAMAGE[i].status_type}${cure}(${this.current_attr.hp})`);
                    this.vue.REPORT_HEAP.push(report);

                    initiator.STATISTICS.cure += cure;
                }
                
            }

            if(this.STATUS_DAMAGE[i]){
                this.STATUS_DAMAGE[i].remain_time -= 1;
                if(this.STATUS_DAMAGE[i].remain_time > 0){
                    temp.push(this.STATUS_DAMAGE[i])
                }
                else{
                    let report = new Report();
                    report.InitVersaReport(this,1,`${this.STATUS_DAMAGE[i].name}${this.STATUS_DAMAGE[i].status_type}`)
                    this.vue.REPORT_HEAP.push(report);
                }
            }
        }

        this.STATUS_DAMAGE = temp;
        
    }
