import Vue from "vue";
import Vuex from 'vuex';

import Warrior from '../mixin/structure/WarriorBattle'

//VueVuex
Vue.use(Vuex);

const store = new Vuex.Store({
    //
    state(){
        return{
            teams : [
                //
                {} =  [
                    {} = new Warrior(2,0),
                    {} = new Warrior(1,0),
                    {} = new Warrior(0,0),//
                ],
                //
                {} = [
                    {} = new Warrior(2,1),//
                    {} = new Warrior(1,1),
                    {} = new Warrior(0,1),
                ]
            ],
        }
    },
    getters:{

    },
    //
    mutations:{
        CommitSimBattleResultTeams(state,arr){
            state.teams = arr;
        }
    },
    //
    actions:{

    },
    modules:{

    }
})


export default store;

