class MyWarrior{
    //
    constructor(data){
        this.id = data.id;
        this.red = data.red;
        this.isUp = data.isUp;
        this.isUpWork = data.isUpWork;
        this.isUpArms =  data.isUpArms;
        this.own = true;
    }
    //
    getSomeValue(data){
        this.red = data.red;
        this.isUp = data.isUp;
        this.isUpWork = data.isUpWork;
        this.isUpArms =  data.isUpArms;
    }

    //
    addRed(){
        this.red++;
    }
    //
    subRed(){
        this.red--;
    }
    //
    isUpSign(status){
        if(status){
            this.isUp = true;
        }else{
            this.isUp = false;
        }
    }
    //
    isUpWorkSign(status){
        if(status){
            this.isUpWork = true;
        }else{
            this.isUpWork = false;
        }
    }
    //
    isUpArmsSign(status){
        if(status){
            this.isUpArms = true;
        }else{
            this.isUpArms = false;
        }
    }
}


export default MyWarrior