class Report{
    constructor(){
        this.type =  '';//
        this.belong = "";//
        this.attacker_img = "";//
        this.receiver_img = "";//

        this.level = 0,//

        this.text1 = '';//1
        this.text2 = '';//2
        this.text3 = "";
        this.text4 = "";
        this.text5 = "";
    }
    //
    InitSysMsg(type,text){
        this.type = type;
        this.text1 = text;
        this.level = 0;
    }

    //,typeattackerreceiverdamage
    InitAttackReport(type,attacker,receiver,damage){
        this.belong = attacker.belong;
        this.level = 1;
        if(type == 'attack'){
            //
            //AB{damageBBcurrent_attr.hp}{}
            this.type = 'attack';
            this.attacker_img = attacker.img.head;
            this.receiver_img = receiver.img.head;
            this.text1 = ``
            this.text2 = `${damage}(${receiver.current_attr.hp})`
        }else if(type == 'ballistic_attack'){
            this.type = 'ballistic_attack';
            this.attacker_img = attacker.img.head;
            this.receiver_img = receiver.img.head;
            this.text1 = `${damage}`
            this.text2 = `(${receiver.current_attr.hp})`
        }
    }

    //
    InitTeamInfo(target){
        this.type = 'info',
        this.level = 0;
        this.belong = target.belong;
        this.attacker_img = target.img.head;
        this.text1 = `${target.camp}-${target.name}-${target.arms}:${target.origin_attr.hp}`
    }

    //
    InitTacticsPrepareExecute(target,index){
        this.level = 0;
        this.type = 'info',
        this.belong = target.belong;
        this.attacker_img = target.img.head;
        this.text1 = `${target.TACTICS_STACK[index].info}`;
    }


    //
    InitVersaReport(target,level,info){
        this.level = level;
        this.type = 'info',
        this.belong = target.belong;
        this.attacker_img = target.img.head;
        this.text1 = info;
    }

    
}

export default Report;