import Report from '../structure/Report'

let jin_warriors = [
    //
    {
        id:1510,
        name : '',
        tactics:{
            name : '',
            quality : 'S',
            main_type : 'passive',//
            second_type : '',//
            third_type : '',//
            info : '50%5250.0%35%5250.0%'
            +'35%5250.0%',
            odd : 100,//
            att : 1,//
            target_type : '',//
            target_number : 1,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            //
            action(self,vue){
                let tag = this;
                let execute = function(){
                    let all_tact_my = 0;
                    let all_tact_en = 0;
                    let all_hp_my = 0;
                    let all_hp_en = 0;
                    let all_ad_my = 0;
                    let all_ad_en = 0;

                    for(let i=0;i<3;i++){
                        all_tact_my += vue.teams[self.belong][i].current_attr.tact;
                        all_hp_my += vue.teams[self.belong][i].current_attr.hp;
                        all_ad_my += vue.teams[self.belong][i].current_attr.ad;

                        all_tact_en += vue.teams[(self.belong + 1)%2][i].current_attr.tact;
                        all_hp_en += vue.teams[(self.belong + 1)%2][i].current_attr.hp;
                        all_ad_en += vue.teams[(self.belong + 1)%2][i].current_attr.ad;
                    }
                    if(all_hp_en <= all_hp_my){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1){
                            let report = new Report();
                            report.InitVersaReport(self,1,``);
                            vue.REPORT_HEAP.push(report);
                            let res = self.ReturnIndexOfTactics(self,5,2,true,false);
                            if(!res){
                                return;
                            }
                            if(res){
                                for(let j=0;j<res.length;j++){
                                    let target = vue.teams[res[j].teams_index][res[j].warrior_index];
                                    //
                                    let damage = self.CountActiveTactDamage(self,target,50,false);
                                    target.GetDamage(damage,tag,self);

                                    //
                                    target.ExecuteAttackedBlock(self);
                                }
                            }
                        }else{
                            let report = new Report();
                            report.InitVersaReport(self,1,``);
                            vue.REPORT_HEAP.push(report);
                        }
                    }
                    if(all_tact_en <= all_tact_my){
                        let rand = Math.floor(Math.random()*100);
                        if(rand  < 35){
                            let report = new Report();
                            report.InitVersaReport(self,1,``);
                            vue.REPORT_HEAP.push(report);
                            let res = self.ReturnIndexOfTactics(self,5,2,true,false);
                            if(!res){
                                return;
                            }
                            if(res){
                                for(let j=0;j<res.length;j++){
                                    let target = vue.teams[res[j].teams_index][res[j].warrior_index];
                                    //
                                    let damage = self.CountActiveTactDamage(self,target,50,false);
                                    target.GetDamage(damage,tag,self);

                                    //
                                    target.ExecuteAttackedBlock(self);
                                }
                            }
                        }else{
                            let report = new Report();
                            report.InitVersaReport(self,1,``);
                            vue.REPORT_HEAP.push(report);
                        }
                    }
                    if(all_ad_en <= all_ad_my){
                        let rand = Math.floor(Math.random()*100);
                        if(rand  == 35){
                            let report = new Report();
                            report.InitVersaReport(self,1,``);
                            vue.REPORT_HEAP.push(report);
                            let res = self.ReturnIndexOfTactics(self,5,2,true,false);
                            if(!res){
                                return;
                            }
                            if(res){
                                for(let j=0;j<res.length;j++){
                                    let target = vue.teams[res[j].teams_index][res[j].warrior_index];
                                    //
                                    let damage = self.CountActiveAdDamage(self,target,50,false);
                                    target.GetDamage(damage,tag,self);

                                    //
                                    target.ExecuteAttackedBlock(self);
                                }
                            }
                        }else{
                            let report = new Report();
                            report.InitVersaReport(self,1,``);
                            vue.REPORT_HEAP.push(report);
                        }
                    }
                    //
                    tag.statistics.times += 1;
                }

                self.CreateAheadExecuteBlock(self,self,0,execute,"command",99,this.name,0);
                let report = new Report();
                report.InitVersaReport(self,1,``);
                vue.REPORT_HEAP.push(report);
                this.statistics.times += 1;
                
            }
        }
    },
    //
    {
        id:1530,
        name : '',
        tactics:{
            name : '',
            quality : 'S',
            main_type : 'command',//
            second_type : '',//
            third_type : '',//
            info : '24%',
            odd : 100,//
            att : 5,//
            target_type : '',//
            target_number : 2,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            //
            action(self,vue){
                let value = Math.floor((self.current_attr.tact -120) * 0.07);
                if(value < 0){
                    value = 0;
                }
                let de_index = 24 + value;
                for(let i=0;i<3;i++){
                    if(vue.teams[self.belong][i].name != ''){
                        let target = vue.teams[self.belong][i];
                    
                        target.current_attr.ad += Math.floor((de_index/100) * target.current_attr.ad);
                        target.current_attr.de += Math.floor((de_index/100) * target.current_attr.de);
                        target.current_attr.tact += Math.floor((de_index/100) * target.current_attr.tact);
                        target.current_attr.speed += Math.floor((de_index/100) * target.current_attr.speed);
                        let flag1 = self.CreateAttrVarBlock(target,target,false,"command","",de_index,99,false,this.name)
                        let flag2 = self.CreateAttrVarBlock(target,target,false,"command","",de_index,99,false,this.name)
                        let flag3 = self.CreateAttrVarBlock(target,target,false,"command","",de_index,99,false,this.name)
                        let flag4 = self.CreateAttrVarBlock(target,target,false,"command","",de_index,99,false,this.name)
                        if(!flag1){
                            let report = new Report();
                            report.InitVersaReport(target,1,`${de_index}%(${target.current_attr.ad})`);
                            vue.REPORT_HEAP.push(report);
                        }
                        if(!flag2){
                            let report = new Report();
                            report.InitVersaReport(target,1,`${de_index}%(${target.current_attr.de})`);
                            vue.REPORT_HEAP.push(report);
                        }
                        if(!flag3){
                            let report = new Report();
                            report.InitVersaReport(target,1,`${de_index}%(${target.current_attr.tact})`);
                            vue.REPORT_HEAP.push(report);
                        }
                        if(!flag4){
                            let report = new Report();
                            report.InitVersaReport(target,1,`${de_index}%(${target.current_attr.speed})`);
                            vue.REPORT_HEAP.push(report);
                        }
                    }
                    

                    
                }
                
                //
                this.statistics.times += 1;
            }
        }
    },
    //
    {
        id:8007,
        name : '',
        tactics:{
            name : '',
            quality : 'A',
            main_type : 'command',//
            second_type : '',//
            third_type : '',//
            info : '10010040%',
            odd : 100,//
            att : 5,//
            target_type : '',//
            target_number : 3,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            action(self,vue){
                let min_speed = 10000;
                let min_index = -1;
                let high_ad = 0;
                let high_ad_index = -1;
                let high_tact = 0;
                let high_tact_index = -1;
                let min_de = 10000;
                let min_de_index = -1;
                for(let i=0;i<3;i++){
                    let target = vue.teams[self.belong][i];
                    if(target.current_attr.speed <= min_speed){
                        min_index = i;
                        min_speed = target.current_attr.speed;
                    }

                    if(target.current_attr.de <= min_de){
                        min_de = target.current_attr.de;
                        min_de_index = i;
                    }

                    if(target.current_attr.tact >= high_tact){
                        high_tact = target.current_attr.tact;
                        high_tact_index = i;
                    }

                    if(target.current_attr.ad >= high_ad){
                        high_ad = target.current_attr.ad;
                        high_ad_index = i;
                    }
                }


                let ad_max = vue.teams[self.belong][high_ad_index];
                let tact_max = vue.teams[self.belong][high_tact_index];
                let de_min = vue.teams[self.belong][min_de_index];
                let speed_min = vue.teams[self.belong][min_index];
                //
                ad_max.current_attr.ad += 100;
                let report = new Report();
                report.InitVersaReport(ad_max,1,`100(${ad_max.current_attr.ad})`);
                vue.REPORT_HEAP.push(report);
                //
                tact_max.current_attr.tact += 100;
                let report1 = new Report();
                report1.InitVersaReport(tact_max,1,`100(${tact_max.current_attr.tact})`);
                vue.REPORT_HEAP.push(report1);
                //
                let value = 40 + Math.floor((self.current_attr.tact - 80) * 0.15);
                let conflict1 = self.CreateDamageVarBlock(de_min,de_min,true,'command','ad',false,false,value,4,false,this.name);      
                if(!conflict1){
                    let report = new Report();
                    report.InitVersaReport(de_min,1,`${value}%`);
                    vue.REPORT_HEAP.push(report);
                }
                //
                let collision = self.CreateStatusVarBlock(speed_min,speed_min,true,"command","",0,4,false,this.name);
                if(!collision){
                    let report = new Report();
                    report.InitVersaReport(speed_min,1,`()`);
                    vue.REPORT_HEAP.push(report);
                }
                
                
                //
                this.statistics.times += 1;
            }
        }
    },
    //
    {
        id:8008,
        name : '',
        tactics:{
            name : '',
            quality : 'S',
            main_type : 'active',//
            second_type : 'damage',//
            third_type : '',//
            info : "175.0%2165.0%",
            odd : 40,//
            att : 5,//
            target_type : '',//
            target_number : 1,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            //
            action(self,vue){
                let tag = this;
                //
                let min_tact = 100000;
                let index = 0;
                for(let i=0;i<3;i++){
                    if(vue.teams[self.belong][i].name != "" && vue.teams[self.belong][i].exist){
                        if(vue.teams[self.belong][i].current_attr.tact < min_tact){
                            min_tact = vue.teams[self.belong][i].current_attr.tact;
                            index = i;
                        }
                    }
                }
                //
                if(min_tact == 100000){
                    return;
                }
                //
                let teammate = vue.teams[self.belong][index];
                let report1 = new Report();
                report1.InitVersaReport(teammate,2,``);
                vue.REPORT_HEAP.push(report1);
                for(let i=0;i<1;i++){
                    let target = vue.teams[(self.belong + 1)%2][i];
                    if(target.current_attr.hp > 0){
                        let damage_index = 175;
                        let damage = self.CountAdDamage(teammate,target,damage_index,false,false);
                        target.current_attr.hp -= damage;
                        if(target.current_attr.hp < 0){
                            target.current_attr.hp = 0;
                            target.exist = false;
                        }
                        let report = new Report();
                        report.InitVersaReport(target,2,`${damage}(${target.current_attr.hp})`);
                        vue.REPORT_HEAP.push(report);
                        target.ExecuteAttackedBlock(self);

                        //
                        tag.statistics.damage += damage;
                        teammate.STATISTICS.tact += damage;
                    }
                }

                //
                let report2 = new Report();
                report2.InitVersaReport(self,2,``);
                vue.REPORT_HEAP.push(report2);
                let att = this.att + self.ReturnTacticsAttAddition(0);
                // let target_number = this.target_number + self.ReturnTacticsTargetNumberAddition(0);
                //
                let en_res = self.ReturnIndexOfTactics(self,att,2,true,false);
                // let my_res = self.ReturnIndexOfTactics(self,att,target_number,false,true);
                //
                if(!en_res){
                    let report_1 = new Report();
                    report_1.InitVersaReport(self,1,``);
                    vue.REPORT_HEAP.push(report_1);
                    return;
                }
                for(let i=0;i<en_res.length;i++){
                    let target = vue.teams[en_res[i].teams_index][en_res[i].warrior_index];
                    //
                    let damage = self.CountActiveTactDamage(self,target,165,false);
                    target.GetDamage(damage,this,self);

                    //
                    target.ExecuteAttackedBlock(self);

                }
                
                //
                this.statistics.times += 1;
            }
        }
    },
    //
    {
        id:8010,
        name : '',
        tactics:{
            name : '',
            quality : 'S',
            main_type : 'combo',//
            second_type : '',//
            third_type : '',//
            info : "230.0%320%",
            odd : 50,//
            att : 1,//
            target_type : '',//
            target_number : 1,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            //
            action(self,target,vue){
                //
                if(target.exist){
                    //
                    let damage = 0;
                    if(target.current_attr.hp <= self.current_attr.hp){
                        //
                        let report = new Report();
                        report.InitVersaReport(self,2,``);
                        vue.REPORT_HEAP.push(report);
                        damage = self.CountComboAdDamage(self,target,320,false,false);
                    }
                    else{
                        damage = self.CountComboAdDamage(self,target,230,false,false);
                    }
                    
                    target.current_attr.hp -= damage;
                    if(target.current_attr.hp < 0){
                        target.current_attr.hp = 0;
                        target.exist = false;
                    }
                    
                    let report = new Report();
                    report.InitVersaReport(target,2,`${damage}(${target.current_attr.hp})`);
                    vue.REPORT_HEAP.push(report);
                    
                    target.ExecuteAttackedBlock(self);

                    //
                    self.STATISTICS.tact += damage;

                    this.statistics.damage += damage;
                    this.statistics.times += 1;

                }
                else{
                    let report = new Report();
                    report.InitVersaReport(target,2,``);
                    vue.REPORT_HEAP.push(report);
                }
            }
        }
    },
    //
    {
        id:8015,
        name : '',
        tactics:{
            name : '',
            quality : 'S',
            main_type : 'command',//
            second_type : '',//
            third_type : '',//
            info : '10%-30%',
            odd : 100,//
            att : 5,//
            target_type : '',//
            target_number : 3,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            //
            action(self,vue){
                let res = self.ReturnIndexOfTactics(self,this.att,this.target_number,true,false);
                
                if(!res){
                    let report = new Report();
                    report.InitVersaReport(self,1,``);
                    vue.REPORT_HEAP.push(report);
                    return;
                }
                for(let i=0;i<res.length;i++){
                    let target = vue.teams[res[i].teams_index][res[i].warrior_index];
                    let value =  Math.floor((self.origin_attr.tact - 80) * 0.11);
                    if(value < 0){
                        value  = 0;
                    }
                    let index = value;//
                    if(target.position == 2){
                        index += 30;
                    }
                    else if(target.position == 1){
                        index += 20;
                    }else{
                        index += 10;
                    }
                    let conflict1 = self.CreateDamageVarBlock(target,target,false,'command','ad',false,true,index,4,false,this.name);
                    let conflict2 = self.CreateDamageVarBlock(target,target,false,'command','tact',false,true,index,4,false,this.name);
                    if(!conflict1){
                        let report = new Report();
                        report.InitVersaReport(vue.teams[res[i].teams_index][res[i].warrior_index],1,`${index}%`);
                        vue.REPORT_HEAP.push(report);
                    }
                    if(!conflict2){
                        let report = new Report();
                        report.InitVersaReport(vue.teams[res[i].teams_index][res[i].warrior_index],1,`${index}%`);
                        vue.REPORT_HEAP.push(report);
                    }
                }
                //
                this.statistics.times += 1;
            }
        }
    },
    

    //
    {
        id:10350,
        name : '',
        tactics:{
            name : '',
            quality : 'B',
            main_type : 'active',//
            second_type : 'cure',//
            third_type : '',//
            info : "180.0%50.0%",
            odd : 75,//
            att : 3,//
            target_type : '',//
            target_number : 1,//
            arms : [''],//
            wait:0,//
            statistics : {
                damage : 0,
                cure : 0,
                times : 0,
            },//
            //
            action(self,vue){
                let att = this.att + self.ReturnTacticsAttAddition(0);
                // let target_number = this.target_number + self.ReturnTacticsTargetNumberAddition(0);
                //
                let res = self.ReturnIndexOfTactics(self,att,1,false,false);
                if(!res){
                    let report = new Report();
                    report.InitVersaReport(self,1,"");
                    vue.REPORT_HEAP.push(report);
                    return;
                }
                for(let i=0;i<res.length;i++){
                    let target = vue.teams[res[i].teams_index][res[i].warrior_index];
                    //
                    let temp1  =[];
                    for(let i=0;i<target.DAMAGE_VAR_HEAP.length;i++){
                        //,
                        if(target.DAMAGE_VAR_HEAP[i].benefit || target.DAMAGE_VAR_HEAP[i].tactics_type == 'command'){
                            temp1.push(target.DAMAGE_VAR_HEAP[i]);
                        }
                        else{
                            let damage_type_str = "";
                            if(target.DAMAGE_VAR_HEAP[i].damage_type == 'tact'){
                                damage_type_str = ""
                            }
                            else if(target.DAMAGE_VAR_HEAP[i].damage_type == 'active'){
                                damage_type_str = ""
                            }
                            let report = new Report();
                            report.InitVersaReport(target,2,`${target.DAMAGE_VAR_HEAP[i].attack == true?"":""}${damage_type_str}${target.DAMAGE_VAR_HEAP[i].addition == true?"":""}${target.DAMAGE_VAR_HEAP[i].value}%`)
                            vue.REPORT_HEAP.push(report)
                        }
                    }
                    target.DAMAGE_VAR_HEAP = temp1;

                    //
                    let temp2 = [];
                    for(let i=0;i<target.STATUS.length;i++){
                        if(target.STATUS[i].benefit || target.STATUS[i].tactics_type == 'command'){
                            temp2.push(target.STATUS[i]);
                        }
                        else{
                            let report = new Report();
                            report.InitVersaReport(target,2,`${target.STATUS[i].status_type}`)
                            vue.REPORT_HEAP.push(report)
                        }
                    }
                    target.STATUS = temp2;

                    //
                    let temp3 = [];
                    for(let i=0;i<target.ATTR_VAR_HEAP.length;i++){
                        if(target.ATTR_VAR_HEAP[i].benefit || target.ATTR_VAR_HEAP[i].tactics_type == 'command'){
                            temp3.push(target.ATTR_VAR_HEAP[i]);
                        }
                        else{
                            //
                            target.ATTR_VAR_HEAP[i].back(target);
                            let report = new Report();
                            report.InitVersaReport(target,2,`${target.ATTR_VAR_HEAP[i].attr_type}${target.ATTR_VAR_HEAP[i].value}`)
                            vue.REPORT_HEAP.push(report)
                        }
                    }
                    target.ATTR_VAR_HEAP = temp3;

                    //
                    let temp4 = [];
                    for(let i=0;i<target.STATUS_DAMAGE.length;i++){
                        if(target.STATUS_DAMAGE[i].benefit || target.STATUS_DAMAGE[i].tactics_type == 'command'){
                            temp4.push(target.STATUS_DAMAGE[i]);
                        }
                        else{
                            let report = new Report();
                            report.InitVersaReport(target,2,`${target.STATUS_DAMAGE[i].status_type}`)
                            vue.REPORT_HEAP.push(report)
                        }
                    }
                    target.STATUS_DAMAGE = temp4;

                    //
                    let cure = self.CountCure(self,target,"active",180,6);
                    if(target.IsCannotCure()){
                        let report = new Report();
                        report.InitVersaReport(target,1,``);
                        vue.REPORT_HEAP.push(report);
                    }
                    else{
                        target.current_attr.hp += cure;
                        let report = new Report();
                        report.InitVersaReport(target,2,`${cure}(${target.current_attr.hp})`);
                        vue.REPORT_HEAP.push(report);
                        //
                        self.STATISTICS.cure += cure;
                        this.statistics.cure += cure;
                    }

                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1){
                        let collision = self.CreateStatusVarBlock(target,target,true,"active","",0,3,false,this.name);
                        if(!collision){
                            let report = new Report();
                            report.InitVersaReport(target,2,``);
                            vue.REPORT_HEAP.push(report);
                        }
                    }
                }
                
                //
                this.statistics.times += 1;
            }
        }
    },
]



export default jin_warriors;